import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OrderInterfaceComponent } from './order-interface.component';
import { Routes, RouterModule } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule } from '@angular/forms';
const routes: Routes = [
  {
    path: '',
    component: OrderInterfaceComponent
  }
]

@NgModule({
  declarations: [OrderInterfaceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
  ]
})
export class OrderInterfaceModule { }
