import { Component, OnInit } from '@angular/core';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-order-interface',
  templateUrl: './order-interface.component.html',
  styleUrls: ['./order-interface.component.scss']
})
export class OrderInterfaceComponent implements OnInit {

  public order: any = JSON.parse(localStorage.getItem("vh-cafe-order-interface"));
  showNote: boolean = false;

  constructor(
    private vhComponent: VhComponent,
    private languageService: LanguageService,

  ) { }

  ngOnInit(): void {
  }

  public updateInterface(type: number) {
    if (this.order.interface != type) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate('Are you sure to change the order interface')}?`, "OK", this.languageService.translate('Cancel')).then(() => {
        const updateSetting = () => {
          this.order.interface = type;
          localStorage.setItem("vh-cafe-order-interface", JSON.stringify(this.order))
        }
        Promise.all([this.vhComponent.showLoading("", "transparent-loading", null, null, false), updateSetting()])
          .finally(() => this.vhComponent.hideLoading(0))
      }, () => { })
    }
  }

}
